@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@400;500;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #ffffff;
  color: #000000;
  background-image: url('https://loremflickr.com/1920/1080?random=1');
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.clicked {
  color: #7f8c8d;
}

.form,
.greetings {
  display: none;
}

.showing {
  display: block;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.bgImage {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  animation: fadeIn 0.5s linear;
  object-fit: contain;
}

.js-clock {
  font-size: 60px;
  margin-top: -70px;
  margin-bottom: -45px;
  max-width: 420px;
}

.js-clock h1,
.canterInformation h4 {
  background-color: #ffffff83;
  color: rgb(0, 0, 0);
  border-radius: 20px;
  padding: 30px;
}

.canterInformation {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.js-weather {
  max-width: 420px;
  font-size: 35px;
  background-color: rgba(255, 255, 255, 0.445);
  color: rgb(0, 0, 0);
  border-radius: 20px;
  padding: 10px 20px 10px 20px;
  margin-top: 30px;
}

form input {
  all: unset;
  background-color: rgba(255, 255, 255, 0.603);
  width: 400px;
  height: 40px;
  font-size: 30px;
  border-radius: 20px;
  padding: 10px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.TodoTemplate{width:420px;margin-left:auto;margin-right:auto;margin-top:0rem;border-radius:20px;overflow:hidden}.TodoTemplate .app-title{background:#22b8cf85;color:#000;height:4rem;font-size:1.5rem;font-weight:700;text-shadow:1px 1px 0px #dadada;display:flex;align-items:center;justify-content:center}.TodoTemplate .content{background:#959e9a85}

.TodoInsert{display:flex;background:#87b9dfd3}.TodoInsert input{background:none;outline:none;border:none;padding:0.5rem;font-size:1.125rem;font-weight:500;margin-left:10px;line-height:1.5;color:#222;flex:1 1}.TodoInsert input::-webkit-input-placeholder{color:#000}.TodoInsert input:-ms-input-placeholder{color:#000}.TodoInsert input::placeholder{color:#000}.TodoInsert button{background:none;outline:none;border:none;background:#868e96;color:white;padding-left:1rem;padding-right:1rem;font-size:1.5rem;display:flex;align-items:center;cursor:pointer;transition:0.1s background ease-in}.TodoInsert button:hover{background:#adb5bd}

.TodoListItem{padding:1rem;display:flex;align-items:center}.TodoListItem:nth-child(even){background:#f8f9fa81}.TodoListItem .checkbox{cursor:pointer;flex:1 1;display:flex;align-items:center}.TodoListItem .checkbox svg{font-size:1.5rem}.TodoListItem .checkbox .text{color:#0c0c0c;margin-left:0.5rem;font-weight:700;text-shadow:1px 1px 0px #969696;flex:1 1}.TodoListItem .checkbox.checked svg{color:#22b8cf}.TodoListItem .checkbox.checked .text{color:#adb5bd;text-decoration:line-through}.TodoListItem .remove{display:flex;align-items:center;font-size:1.5rem;color:#ff6b6b;cursor:pointer}.TodoListItem .remove:hover{color:#ff8787}.TodoListItem+.TodoListItem{border-top:1px solid #dee2e6}

.TodoList{min-height:320px;max-height:513px;overflow-y:auto}

